<template>
  <div class="left" :class="{'left-open':GLOBAL.left_open}">
	<input type="file" ref="file1" name="avatar" accept="application/vnd.openxmlformats-officedocument.spreadsheetml.sheet, application/vnd.ms-excel" @change="import_data($event,Function.import_data_driver)" style="display:none;" /> 
      <div class="left-d">
	    <div class="left-d-d" v-for="(item,index) in list" @click="change_list(index)">
		  <i class="left-d-d-icon" :class="item.img"></i>
		  <div class="left-d-d-d">{{item.title}}</div>
	    </div>
	    <div class="left-d-d"><router-link to="/Left">Go to Left</router-link></div>
	    <div class="left-d-d" @click="show_note">call alert</div>
      </div>
	  <div class="left-d-more" :class="{'left-d-more-show':GLOBAL.left_more_show}">
	  <router-link v-for="(item,index) in list[c_list].c_title" :to="item.url" v-if="item.url">
	    <div class="left-d-more-d">{{item.title}}</div>
	  </router-link>
	  <div class="left-d-more-d" @click="$refs[item.open_input].click()" v-else>{{item.title}}</div>
	  <div class="left-d-more-d" @click="GLOBAL.left_more_show=!GLOBAL.left_more_show">收回</div>
	</div>
  </div>
</template>

<script>
export default {
  data() {
    return {
	  c_list:0,
	  list:[
	    {title:"控制台",url:"/CaseList/",img:"el-icon-data-analysis",no:1,c_title:[
		  {title:"本日接送狀況",url:"/Chart1",img:"",no:2},
		  {title:"車輛使用率比較表",url:"/Chart2",img:"",no:3},
		  {title:"營收報表",img:"",url:"/Chart3",no:4},
		  {title:"出發區域比率",url:"/Chart4",img:"",no:5},
		  {title:"返回區域比率",url:"/Chart5",img:"",no:6},
		  {title:"區域月報表",url:"/Chart6",img:"",no:7},
		  {title:"醫療院所比例",url:"/Chart7",img:"",no:8},
		]},
		{title:"預約訂單",url:"/CaseList/",img:"el-icon-document",no:9,c_title:[
		  {title:"全部資料",url:"/OrderList/1",img:"",no:10},
		  {title:"今日任務",url:"/CaseList/",img:"",no:11},
		  {title:"本週任務",url:"/CaseList/",img:"",no:12},
		  {title:"本月任務",url:"/CaseList/",img:"",no:13},
		  {title:"今年任務",url:"/CaseList/",img:"",no:14},
		]},
		{title:"派車調度",url:"/CaseList/",img:"el-icon-truck",no:15,c_title:[
		  {title:"調度單搜尋",url:"/CaseList/",img:"",no:16},
		  {title:"調度控制台",url:"/DragDispatch/",img:"",no:17},
		  {title:"調度控制台-表格式",url:"/TableDispatch/",img:"",no:18},
		]},
		{title:"個案管理",url:"/CaseList/",img:"el-icon-user",no:19,c_title:[
		  {title:"全部個案",url:"/CaseList/1?key=&pMode=1",img:"",no:20},
		  {title:"自費個案",url:"/CaseList/",img:"",no:21},
		  {title:"公費個案",url:"/CaseList/",img:"",no:22},
		  {title:"結案",url:"/CaseList/",img:"",no:23},
		]},
		{title:"司機車輛管理",url:"/DriverList/",img:"el-icon-date",no:24,c_title:[
		  {title:"駕駛資料",url:"/DriverList/1?key=&status=",img:"",no:25},
		  {title:"車輛資料",url:"/CarList/1?key=&Status=&carType=0",img:"",no:28},
		]},
		{title:"系統資料管理",url:"/CaseList/",img:"el-icon-setting",no:31,c_title:[
		  {title:"輔助車資參數",url:"/AreaSubsidy/",img:"",no:32},
		  {title:"司機車輛設定",url:"/CarsTypeList/",img:"",no:33},
		  {title:"營運單位一覽表",url:"/CompanyList/",img:"",no:33},
		  {title:"A單位一覽表",url:"/ACompanyList/1",img:"",no:33},
		  {title:"核定地點管理",url:"/AddrTurnNameList/1",img:"",no:34},
		  {title:"系統管理員設定",url:"/AdminList/1",img:"",no:35},
		]},
		{title:"匯入",url:false,img:"el-icon-upload2",no:31,c_title:[
		  {title:"匯入司機/車輛",url:false,img:"",no:33,open_input:"file1"},
		]},
		{title:"匯出",url:false,img:"el-icon-download",no:31,c_title:[
		  {title:"匯出服務紀錄",url:false,img:"",no:32},
		  {title:"匯出任務明細檔",url:false,img:"",no:33},
		]},
	  ]
	}
  },
  mounted:async function() {
    console.log(this.$route)
  },
  watch:{
    async $route(to, from){
	  this.GLOBAL.left_open=false
	  this.GLOBAL.left_more_show=false
    }
  },
  methods:{
    get_path:function(x) {
	  var arr=x.split("/")
	  console.log(arr)
	  return arr
	},
	change_list:function(x) {
	  this.GLOBAL.left_more_show=true
	  this.c_list=x;
	},
    reload:function() {
	  m.reload()
	},
	show_note:function() {
	  var data=moment().format('HH:mm:ss')
      this.$notify({
        title: '收到新訂單',
        dangerouslyUseHTMLString: true,
		duration:10000,
        message: '<div">訂單編號:TS5555123<br>'+data+'</div>'
      });
	},
	import_data:async function(e,callback) {
	  this.GLOBAL.show_black=true
      let file=e.target.files[0]
      let formData = new FormData();
      formData.append('file', file);
	  alert(await callback(formData))
	  this.GLOBAL.show_black=false
	}
  }
}
</script>


