<template>
  <div class="chart">
    <h2>本日接送狀況</h2>
	<canvas width="400" height="400" ref="c1"></canvas>
  </div>
</template>

<script>
export default {
  data() {
    return {
	  list:[],
      ctxt:""
	}
  },
  mounted:async function() {
    this.GLOBAL.show_black=true
	await this.all_ready()
    await this.get_data()
	this.GLOBAL.show_black=false
	console.log(this.list)
  },
  watch:{
    async $route(to, from){
    }
  },
  methods:{
    get_data:async function(x) {
	  this.list=await this.Function.get_GetDatePickWeekly()	  
			  var arr1=this.list.map(x=>x.ReportPickViewModel.AchievedRate)
			  var arr2=this.list.map(x=>x.ReportPickViewModel.CompleteCount)
			  var arr3=this.list.map(x=>x.ReportPickViewModel.MissCount)
			  var arr4=this.list.map(x=>x.ReportPickViewModel.NotPerformedCount)
			  var arr5=this.list.map(x=>x.ReportPickViewModel.TotalCount)
			  var arr6=this.list.map(x=>x.DetailDate)
		      var obj=[
                {
	              label: '已完成',
				  fill: false,
                  backgroundColor: 'rgb(119, 255, 0)',
				  borderColor: 'rgb(119, 255, 0)',
                  data: arr2
                },
                {
	              label: '空趟',
				  fill: false,
                  backgroundColor: 'rgb(220, 238, 17)',
				  borderColor: 'rgb(220, 238, 17)',
                  data: arr3
                },
                {
	              label: '未執行',
				  fill: false,
                  backgroundColor: 'rgb(238, 28, 17)',
				  borderColor: 'rgb(238, 28, 17)',
                  data: arr4
                },
                {
	              label: '總趟次',
				  fill: false,
                  backgroundColor: 'rgb(17, 153, 238)',
				  borderColor: 'rgb(17, 153, 238)',
                  data: arr5
                },
              ]
              var ctx = this.$refs.c1
              if(this.ctxt) {
                this.ctxt.destroy()
              }
              this.ctxt=this.Function.chart.drew_chart(ctx,'line',arr6,obj,true)
	}
  }
}
</script>



