<template>
  <div class="more">
    <h2>車輛詳細資料</h2>
	<div class="more-b">
	  <div class="more-h">
	    <div class="more-h-d" :class="{'more-h-d-on':!edit}" @click="edit=false">瀏覽</div>
		<div class="more-h-d" :class="{'more-h-d-on':edit}" @click="edit=true">編輯</div>
	  </div>
	  <div class="more-c">
        <div class="more-d">
		  <span>車身顏色: {{list.CarColor}}</span>
		  <div class="more-d-edit">
		    <el-input v-model="list.CarColor" v-if="edit"></el-input>
		  </div>
		  <hr>
		</div>
        <div class="more-d">
		  <span>類別: {{list.CarColor}}</span>
		  <div class="more-d-edit">
		    <el-input v-model="list.CarColor" v-if="edit"></el-input>
		  </div>
		  <hr>
		</div>
        <div class="more-d">
		  <span>車牌號碼: {{list.CarNo}}</span>
		  <div class="more-d-edit">
		    <el-input v-model="list.CarNo" v-if="edit"></el-input>
		  </div>
		  <hr>
		</div>
        <div class="more-d">
		  <span>廠牌型號: {{list.FactoryType}}</span>
		  <div class="more-d-edit">
		    <el-input v-model="list.FactoryType" v-if="edit"></el-input>
		  </div>
		  <hr>
		</div>
        <div class="more-d">
		  <span>車輛來源: {{list.CarFrom}}</span>
		  <div class="more-d-edit">
		    <el-input v-model="list.CarFrom" v-if="edit"></el-input>
		  </div>
		  <hr>
		</div>
        <div class="more-d">
		  <span>車頂高度: {{list.CarTop}}</span>
		  <div class="more-d-edit">
		    <el-input v-model="list.CarTop" v-if="edit"></el-input>
		  </div>
		  <hr>
		</div>
        <div class="more-d">
		  <span>輪椅數量: {{list.SeatNum}}</span>
		  <div class="more-d-edit">
		    <el-input v-model="list.SeatNum" v-if="edit"></el-input>
		  </div>
		  <hr>
		</div>
        <div class="more-d">
		  <span>輪椅數量: {{list.WheelchairNum}}</span>
		  <div class="more-d-edit">
		    <el-input v-model="list.WheelchairNum" v-if="edit"></el-input>
		  </div>
		  <hr>
		</div>
      </div>
	</div>
  </div>
</template>

<script>
export default {
  data() {
    return {
	  list:[],
	  list_key:[],
	  edit:false
	}
  },
  mounted:async function() {
    this.GLOBAL.show_black=true
	await this.all_ready()
    await this.get_data(this.$route.params.id)
	this.GLOBAL.show_black=false
	console.log(this.list)
  },
  watch:{
    async $route(to, from){
	  this.GLOBAL.show_black=true
      await this.get_data(this.$route.params.id)
	  this.GLOBAL.show_black=false
    }
  },
  methods:{
    get_data:async function(x) {
	  this.list=await this.Function.get_car_more(x)
	  this.list_key=Object.keys(this.list)
	  console.log(this.list)
	}
  }
}
</script>



