<template>
  <div class="chart">
    <h2>營收報表</h2>
	<div class="chart-main">
	  <canvas width="400" height="400" ref="c1"></canvas>
	</div>
	<div class="chart-main">
	  <canvas width="400" height="400" ref="c2"></canvas>
	</div>
  </div>
</template>

<script>
export default {
  data() {
    return {
	  list:[],
	  list2:[],
      ctxt:"",
	  ctxt2:""
	}
  },
  mounted:async function() {
    this.GLOBAL.show_black=true
	await this.all_ready()
    await this.get_data()
	this.GLOBAL.show_black=false
	console.log(this.list)
  },
  watch:{
    async $route(to, from){
    }
  },
  methods:{
    get_data:async function(x) {
	  this.list=await this.Function.get_GetFromArea()
	  this.list2=await this.Function.get_GetBackrea()
            var arr_name = this.list.map(x => x.AreaName)
            var arr1 = this.list.map(x => x.RateOne)
            var arr2 = this.list.map(x => x.RateTwo)
            var arr_color = []
            for (let i in this.list) {
              arr_color.push(this.Function.r_color())
            }
            this.$nextTick(function () {
              var obj = [
                {
                  backgroundColor: arr_color,
                  data: arr1
                }
              ]
              if(this.ctxt) {
                this.ctxt.destroy()
              }
			  this.ctxt = this.Function.chart.drew_chart_remove_lab(this.$refs.c1, 'bar', arr_name, obj)
              if(this.ctxt2) {
                this.ctxt2.destroy()
              }
              this.ctxt2 = this.Function.chart.drew_chart_remove_lab(this.$refs.c2, 'bar', arr_name, obj)
            })
	}
  }
}
</script>



