<template>
  <div class="more">
    <h2>司機詳細資料</h2>
	<div class="more-b">
	  <div class="more-h">
	    <div class="more-h-d" :class="{'more-h-d-on':!edit}" @click="edit=false" v-if="$route.params.id">瀏覽</div>
		<div class="more-h-d" :class="{'more-h-d-on':edit}" @click="edit=true">{{$route.params.id ? "編輯":"新增"}}</div>
	  </div>
	  <div class="more-c">
		<div class="more-c-data">
		  <img class="more-c-data-img" :src="list.Pic ? list.Pic : 'https://api.donkeymove.com//UploadFolder/images/default.png'" @click="show_c()">
		  <el-button type="primary" icon="el-icon-document-checked" @click="sub()">存檔</el-button>
		</div>
        <div class="more-d">
		  <span>姓名: {{list.DriverName}}</span>
		  <div class="more-d-edit" v-if="edit">
		    <el-input placeholder="請輸入姓名" v-model="list.DriverName"></el-input>
		  </div>
		  <hr>
		</div>
        <div class="more-d">
		  <span>手機: {{list.Phone}}</span>
		  <div class="more-d-edit" v-if="edit">
		    <el-input placeholder="請輸入手機:" v-model="list.Phone"></el-input>
		  </div>
		  <hr>
		</div>
        <div class="more-d">
		  <span>性別: {{list.Sex==1 ? "男" : "女"}}</span>
		  <div class="more-d-edit" v-if="edit">
		    <el-input placeholder="請輸入性別" v-model="list.DriverName"></el-input>
		  </div>
		  <hr>
		</div>
        <div class="more-d">
		  <span>市話: {{list.Tel}}</span>
		  <div class="more-d-edit" v-if="edit">
		    <el-input placeholder="請輸入市話" v-model="list.Tel"></el-input>
		  </div>
		  <hr>
		</div>
        <div class="more-d">
		  <span>身分證字號: {{list.UID}}</span>
		  <div class="more-d-edit" v-if="edit">
		    <el-input placeholder="請輸入身分證字號" v-model="list.UID"></el-input>
		  </div>
		  <hr>
		</div>
        <div class="more-d">
		  <span>Email: {{list.Email}}</span>
		  <div class="more-d-edit" v-if="edit">
		    <el-input placeholder="請輸入Email" v-model="list.Email"></el-input>
		  </div>
		  <hr>
		</div>
        <div class="more-d">
		  <span>駕照:<el-tag v-for="(item,index) in list.DriverLicense">{{item.CarTypeName}}</el-tag></span>
		  <div class="more-d-edit" v-if="edit">
		    <el-select value-key="CarTypeName" v-model="list.DriverLicense" multiple placeholder="請選擇" @change="$forceUpdate()">
              <el-option v-for="(item,index) in DriverLicense.data" :key="index" :label="item.CarTypeName" :value="item"></el-option>
            </el-select>
		  </div>
		  <div v-for="(item,index) in list.DriverLicense" v-if="edit">
			<div>{{item.CarTypeName}}</div>
		    <el-input style="width:50%;float:left;" v-model="item.LicenseNo" :placeholder="'請輸入駕照編號'"></el-input>
			<el-input style="width:50%;" v-model="item.ExDate" :placeholder="'請輸入到期日'"></el-input>
		  </div>
		  <hr>
		</div>
        <div class="more-d">
		  <span>保險:<el-tag v-for="(item,index) in list.DriverSecure">{{item}}</el-tag></span>
		  <div class="more-d-edit" v-if="edit">
		    <el-select v-model="list.DriverSecure" multiple placeholder="請選擇">
              <el-option v-for="(item,index) in DriverSecure.data" :key="index" :label="item" :value="item"></el-option>
            </el-select>
		  </div>
		  <hr>
		</div>
        <div class="more-d">
		  <span>持有證照:<el-tag v-for="(item,index) in list.DriverProLicense">{{item}}</el-tag></span>
		  <div class="more-d-edit" v-if="edit">
		    <el-select v-model="list.DriverProLicense" multiple placeholder="請選擇">
              <el-option v-for="(item,index) in DriverProLicense.data" :key="index" :label="item" :value="item"></el-option>
            </el-select>
		  </div>
		  <hr>
		</div>
		<div class="more-c-d" @click="get_punch()" v-if="!edit">
          <div class="more-c-d-h">打卡紀錄</div>
		  <div class="more-c-d-c">
            <el-table :data="punch" style="width: 100%" v-if="punch!==false">
              <el-table-column label="日期" style="width:calc(100% / 3);">
			    <template slot-scope="scope">
                  {{Function.moment.yyyymmdd(scope.row.CreateDate)}}
                </template>
			  </el-table-column>
              <el-table-column label="上班時間" style="width:calc(100% / 3);">
			    <template slot-scope="scope">
                  {{scope.row.OnTime ? Function.moment.yyyymmdd(scope.row.OnTime) : "無"}}
                </template>
			  </el-table-column>
              <el-table-column label="下班時間" style="width:calc(100% / 3);">
			    <template slot-scope="scope">
                  {{scope.row.OffTime ? Function.moment.yyyymmdd(scope.row.OffTime) : "無"}}
                </template>
			  </el-table-column>
            </el-table>		    	
		  </div>
		</div>
      </div>
	</div>
  </div>
</template>

<script>
export default {
  data() {
    return {
	  list:{
  "Id": 0,
  "CompanyId": 0,
  "Account": "",
  "Pwd": "",
  "Sex": "",
  "Pic": "",
  "DriverName": "",
  "Phone": "",
  "UID": "",
  "Tel": "",
  "Email": "",
  "County": "",
  "District": "",
  "Addr": "",
  "Remark": "",
  "Status": 1,
  "IsDeleted": true,
  "Enabled": true,
  "CreateId": 0,
  "CreateBy": "string",
  "CreateTime": "2019-10-24T09:51:59.267Z",
  "ModifyId": 0,
  "ModifyBy": "string",
  "ModifyTime": "2019-10-24T09:51:59.267Z",
  "DriverSaveTypes": [
    {
      "Id": 0,
      "DriverId": 0,
      "TypeId": 160,
      "ExpiredDate": "2019-10-24T09:51:59.267Z"
    }
  ]
},
	  list_key:[],
	  DriverLicense:"",
	  list_DriverLicense:"",
	  DriverSecure:"",
	  list_DriverSecure:"",
	  DriverProLicense:"",
	  list_DriverProLicense:"",
	  edit:false,
	  punch:false,
	  health:""
	}
  },
  mounted:async function() {
	this.GLOBAL.show_black=true
	if(!this.$route.params.id) this.edit=true
	await this.all_ready()
    await this.get_data(this.$route.params.id)
	this.GLOBAL.show_black=false
	console.log(this.list)
  },
  watch:{
    async $route(to, from){
	  this.GLOBAL.show_black=true
      await this.get_data(this.$route.params.id)
	  this.GLOBAL.show_black=false
    }
  },
  methods:{
    get_data:async function(x) {
	  if(x) {
		this.list=await this.Function.get_driver_more_edit(x)
		this.list_DriverLicense=this.list.DriverSaveTypes
	  }
	  this.DriverLicense=this.Function.get_CarsType_parentID(9)
	  //this.DriverLicense.data=this.DriverLicense.data.map(re=>re={CarTypeName:re.CarTypeName,ExDate:"",LicenseNo:""})
	  this.DriverSecure=this.Function.get_CarsType_parentID(206)
	  //this.DriverSecure.data=this.DriverSecure.data.map(re=>re.CarTypeName)
	  this.DriverProLicense=this.Function.get_CarsType_parentID(162)
	  //this.DriverProLicense.data=this.DriverProLicense.data.map(re=>re.CarTypeName)
	  this.list_key=Object.keys(this.list)
	  console.log(this.list)
	},
	show_c:function() {
	  console.log(this.list)
	},
	get_punch:async function() {
	  this.GLOBAL.show_black=true
	  this.punch=await this.Function.get_PunchByDriver(this.$route.params.id)
	  this.GLOBAL.show_black=false
	},
	sub:async function() {
      this.GLOBAL.show_black=true
      if(this.$route.params.id) {
        await this.Function.put_driver(this.list)
	  }
	  else {
        await this.Function.post_driver(this.list)
	  }
      this.GLOBAL.show_black=false
	},
  }
}
</script>



