<template>
  <div class="dispatch">
    <h2>調度控制台</h2>
    <table border="1" class="dispatch-table">
      <tr>
        <th>車輛\時間</th>
        <th v-for="(item,index) in time">{{item}}</th>
      </tr>
      <tr v-for="(item,index) in car">
        <th>{{item.CarNo}}</th>
        <td v-for="(item2,index2) in time">
          <div
            class="dispatch-table-d"
            v-for="(item3,index3) in dispatch"
            v-if="item3.DespatchDetails[0].CarNo==item.CarNo&&item3.time==item2"
          >123</div>
        </td>
      </tr>
    </table>
  </div>
</template>

<script>
import moment from "moment";
export default {
  data() {
    return {
      time: [
        "06:00",
        "06:15",
        "06:30",
        "06:45",
        "07:00",
        "07:15",
        "07:30",
        "07:45",
        "08:00",
        "08:15",
        "08:30",
        "08:45",
        "09:00",
        "09:15",
        "09:30",
        "09:45",
        "10:00",
        "10:15",
        "10:30",
        "10:45",
        "11:00",
        "11:15",
        "11:30",
        "11:45",
        "12:00",
        "12:15",
        "12:30",
        "12:45",
        "13:00",
        "13:15",
        "13:30",
        "13:45",
        "14:00",
        "14:15",
        "14:30",
        "14:45",
        "15:00",
        "15:15",
        "15:30",
        "15:45",
        "16:00",
        "16:15",
        "16:30",
        "16:45",
        "17:00",
        "17:15",
        "17:30",
        "17:45",
        "18:00",
        "18:15",
        "18:30",
        "18:45",
        "19:00",
        "19:15",
        "19:30",
        "19:45",
        "20:00",
        "20:15",
        "20:30",
        "20:45",
        "21:00",
        "21:30",
        "21:45",
        "22:00",
        "22:15",
        "22:30",
        "22:45",
        "23:00"
      ],
      order: "",
      dispatch: "",
      car: "",
      day: moment().format("YYYY-MM-DD")
    };
  },
  mounted: async function() {
    this.GLOBAL.show_black = true;
    await this.all_ready();
    await this.get_data();
    this.GLOBAL.show_black = false;
    console.log(moment().format());
  },
  watch: {
    async $route(to, from) {
      this.GLOBAL.show_black = true;
      await this.get_data();
      this.GLOBAL.show_black = false;
    }
  },
  methods: {
    get_data: async function(x) {
      this.order = await this.Function.get_dispatch_order(this.day);
      this.dispatch = await this.Function.get_dispatch_allgroup(this.day);
      this.car = await this.Function.get_dispatch_car();
      for (let i in this.dispatch) {
        this.dispatch[i].time = moment(
          this.dispatch[i].DespatchDetails[0].OrderDetails.ReservationDate
        ).format("HH:mm");
      }
      console.log(this.dispatch);
    }
  }
};
</script>


