<template>
  <div class="dispatch">
    <h2>調度控制台-表格式</h2>
    <div class="seach">
      <el-col :span="4">
        <el-date-picker v-model="day" type="date" placeholder="選擇日期" value-format="yyyy-MM-dd" @change="get_data">
    </el-date-picker>
      </el-col>
    </div>
    <div class="dispatch-main">
      <div class="dispatch-order" ref="order_d">
        <h3>待調度</h3>
        <div class="dispatch-order-d" v-for="(item,index) in order" draggable="true" @dragstart="drag($event,item)">
          <div>{{item.SOrderNo}}</div>
          <div>{{item.FromAddr}}</div>
          <div>{{item.ToAddr}}</div>
        </div>
      </div>
      <div class="dispatch-car">
        <h3>已調度</h3>
        <div class="dispatch-car-d" v-for="(item,index) in car" v-if="item.DriverInfo" @click="item.open=!item.open;$forceUpdate();" @dragover="allowDrop($event)" @drop="drop($event,item)">
          <div class="dispatch-car-d-d">{{item.CarNo}} ({{item.DriverInfo.DriverName}})</div>
          <div class="dispatch-car-d-dis">
            <div class="dispatch-car-d-dis-d" v-for="(item2,index2) in get_dis_by_driverid(item)">{{item2.DespatchDetails[0].OrderDetails.SOrderNo}}</div>
          </div>
          <el-collapse-transition>
            <div v-if="item.open">
              <DriverStroke :order="get_dis_by_driverid(item)" />
            </div>
          </el-collapse-transition>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import moment from 'moment'
import DriverStroke from './DriverStroke'
export default {
  data() {
    return {
	    order:"",
	    dispatch:"",
	    car:"",
      day:moment().format("YYYY-MM-DD")
	  }
  },
  components: {DriverStroke},
  mounted:async function() {
    var orderd=this.$refs.order_d
    var orderdt=orderd.offsetTop
    this.GLOBAL.show_black=true
	await this.all_ready()
  await this.get_data()
  document.querySelectorAll('.d1-r')[0].addEventListener("scroll",function(e) {
    if(e.target.scrollTop>=200) {
      orderd.style.top=e.target.scrollTop-orderdt+"px"
    }
    else {
      orderd.style.top="0px"
    }
  }, false);
	this.GLOBAL.show_black=false
  },
  watch:{
    async $route(to, from){
      this.GLOBAL.show_black=true
	  await this.get_data()
	  this.GLOBAL.show_black=false
    }
  },
  methods:{
    get_data:async function(x) {
      this.GLOBAL.show_black=true
      this.order=await this.Function.get_dispatch_order(this.day)
	  this.dispatch=await this.Function.get_dispatch_allgroup(this.day)
	  this.car=await this.Function.get_dispatch_car().catch(e=>alert(e))
	  for(let i in this.dispatch) {
	    this.dispatch[i].time=moment(this.dispatch[i].DespatchDetails[0].OrderDetails.ReservationDate).format("HH:mm")
      }
	  for(let i in this.car) {
	    this.car[i].open=false
	  }
      console.log(this.order)
      console.log(this.car)
      console.log(this.dispatch)
      this.GLOBAL.show_black=false
    },
    get_dis_by_driverid:function(x) {
      var arr=[]
      for(let i in this.dispatch) {
        if(x.DriverId==this.dispatch[i].DespatchDetails[0].Despatch.DriverInfoId) {
          arr.push(this.dispatch[i])
        }
      }
      return arr
    },
    drag:function(e,x) {
      e.dataTransfer.setData("Text",JSON.stringify(x))
    },
    allowDrop:function(e) {
      e.preventDefault();
    },
    drop:function(e,x) {
      e.preventDefault()
      var data=JSON.parse(e.dataTransfer.getData("Text"))
      var obj={
        DespatchDetails:[
          {
            Despatch:{DriverInfoId:x.DriverId},
            OrderDetails:data
          }
        ]
      }
      console.log(data)
      this.dispatch.push(obj)
      this.$forceUpdate()
      console.log(this.dispatch)
    }
  }
}
</script>


