<template>
  <div class="list">
    <h2>全部訂單</h2>
	<Seach/>
      <div class="list-d order-list" v-for="(item,index) in list.data">
        <div class="order-list-d1">
          <div>
            <div>訂單編號:{{item.SOrderNo}}</div><hr>
            <div>預約時間:{{Function.moment.yyyymmdd(item.ReservationDate)}}</div>
            <div>狀態:{{GLOBAL.status_arr[item.Status]}}</div>
            <div>行程:{{item.IsBack ? "回程" : "去程"}}</div>
            <div>建立時間:{{Function.moment.yyyymmdd(item.CreateTime)}}</div>
          </div>        
        </div>
        <div class="order-list-d2">
          <div>
            <div>{{item.CanShared ? "可共乘" : "不可共乘"}}</div>
            <div>{{item.FromAddr}}</div>
            <i class="el-icon-bottom"></i>
            <div>{{item.ToAddr}}</div>
          </div>         
        </div>
        <div class="order-list-d3">
          <div>
            <div>狀態:{{GLOBAL.status_arr[item.Status]}}</div>
            <div>個案負擔 ${{item.SelfPayAmt+item.OtherAmt}}</div>
            <div>(車資 + 陪同金額)</div>
            <router-link :to="'/OrderDetails/'+item.Id" style="width: 49%;margin: 0 1% 0 0;"><div class="bt">查看訂單</div></router-link>
            <div class="bt">轉單</div>
          </div>         
        </div>
        <!--<el-row>
          <el-col :xl="5" :md="8" :sm="8" :xs="24" class="order-list-d" style="background-color:var(--color1);color:#fff;">
            <div>
              <div>訂單編號:{{item.SOrderNo}}</div>
              <div>預約時間:{{Function.moment.yyyymmdd(item.ReservationDate)}}</div>
              <div>狀態:{{GLOBAL.status_arr[item.Status]}}</div>
              <div>行程:{{item.IsBack ? "回程" : "去程"}}</div>
              <div>建立時間:{{Function.moment.yyyymmdd(item.CreateTime)}}</div>
            </div>
          </el-col>
          <el-col :xl="14" :md="8" :sm="8" :xs="24" class="order-list-d">
            <div>
              <div>{{item.CanShared ? "可共乘" : "不可共乘"}}</div>
              <div>{{item.FromAddr}}</div>
              <i class="el-icon-bottom"></i>
              <div>{{item.ToAddr}}</div>
            </div>
          </el-col>
          <el-col :xl="5" :md="8" :sm="8" :xs="24" class="order-list-d hidden-md-and-down" style="background-color:var(--color3);color:#fff;">
            <div>
              <div class="bt">轉單</div>
              <div>狀態:{{GLOBAL.status_arr[item.Status]}}</div>
              <div>個案負擔 ${{item.SelfPayAmt+item.OtherAmt}}</div>
              <div>(車資 + 陪同金額)</div>
              <router-link :to="'/OrderDetails/'+item.Id"><div class="bt">查看訂單</div></router-link>
              <div class="bt">訂單歷程</div>
            </div>
          </el-col>
        </el-row>-->
      </div>
    <div class="no-found" v-if="list.data&&list.data.length<1">查無資料</div>
	<Page :max="list.pageCount" :url="'OrderList'"/>
  </div>
</template>

<script>
import Seach from './Seach'
import Page from './Page'
export default {
  data() {
    return {
	  list:[],
	}
  },
  components: {Page,Seach},
  mounted:async function() {
    this.GLOBAL.show_black=true
	await this.all_ready()
    await this.get_data(this.$route.params.page)
	console.log(this.list)
	this.GLOBAL.show_black=false
  },
  watch:{
    async $route(to, from){
	  this.GLOBAL.show_black=true
      await this.get_data(this.$route.params.page)
	  this.GLOBAL.show_black=false
    }
  },
  methods:{
    get_data:async function(x) {
	  this.list=await this.Function.get_OrderDetails(x)
	  console.log(this.list)
	}
  }
}
</script>



